/*----- Основные цвета -----*/
html {
  min-height: 100%;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
/* ===== RESET ===== */
.title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin-top: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
/* # How to hide elements visually */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  min-width: 320px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1150px;
  }
}
.body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  background-color: #ffffff;
  color: #444444;
}
.button {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  padding: 14px 70px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #444444;
  border: 2px solid #444444;
  cursor: pointer;
}
.button--green {
  color: #ffffff;
  border-color: transparent;
  background-color: #63d1bb;
  font-size: 17px;
  line-height: 16px;
}
.button:hover {
  background: linear-gradient(0deg, #444444, #444444);
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.main {
  flex-grow: 1;
}
.header {
  background-color: #f1f1f1;
  color: #000000;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    overflow-x: hidden;
  }
}
.navigation {
  border-bottom: 1px solid #ececec;
  position: relative;
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation--nojs .site-list,
.navigation--nojs .user-list {
  position: static;
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    grid-template-columns: 1fr 348px;
    align-items: center;
    padding: 0 50px;
    border: none;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 240px 1fr;
    max-width: 1150px;
    margin: 0 auto;
    position: static;
  }
}
.navigation__top-menu {
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  padding: 22px 30px 23px 30px;
  position: relative;
}
@media (min-width: 768px) {
  .navigation__top-menu {
    padding: 21px 0 20px 0;
    display: flex;
    border-bottom: 1px solid #ececec;
    align-self: stretch;
  }
}
@media (min-width: 1150px) {
  .navigation__top-menu {
    padding: 6px;
    min-height: 88px;
    order: 2;
  }
  .navigation__top-menu::before {
    width: 0;
  }
}
.navigation__logo {
  grid-column: 2/3;
}
@media (min-width: 768px) {
  .navigation__logo {
    grid-column: 1/2;
  }
  .navigation__logo::before {
    content: "";
    background-color: #ffffff;
    width: 50px;
    height: calc(100% + 1px);
    position: absolute;
    left: -50px;
    top: 0;
    border-bottom: 1px solid #ececec;
  }
}
@media (min-width: 1150px) {
  .navigation__logo {
    margin: 0 auto;
  }
  .navigation__logo::before {
    width: 0;
  }
}
.navigation__logo-img {
  display: block;
}
@media (min-width: 768px) {
  .navigation__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .navigation__logo-img {
    width: 149px;
    height: 72px;
  }
}
.navigation__toggle {
  grid-column: 3/4;
  justify-self: end;
  border: none;
  background-image: url("../img/menu.svg");
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  width: 20px;
  height: 14px;
  cursor: pointer;
}
.navigation__toggle--closed {
  background-image: url("../img/menu-close.svg");
  width: 21px;
  height: 20px;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .navigation__list.site-list {
    display: none;
  }
  .navigation__list.user-list {
    display: none;
  }
  .navigation__list--opened.site-list,
  .navigation__list--opened.user-list {
    display: flex;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-direction: row;
    align-self: stretch;
    align-items: center;
  }
  .navigation__list.site-list {
    order: 2;
    justify-content: start;
    column-gap: 20px;
  }
}
@media (min-width: 1150px) {
  .navigation__list.user-list {
    order: 3;
    justify-content: flex-end;
  }
  .navigation__list.site-list {
    order: 1;
    column-gap: 36px;
  }
}
.navigation__info {
  display: none;
}
@media (min-width: 768px) {
  .navigation__info {
    display: block;
    grid-column: 2/3;
    grid-row: 2/3;
    margin: 0;
    align-self: center;
    justify-self: end;
  }
}
@media (min-width: 1150px) {
  .navigation__info {
    grid-column: 3/4;
    align-self: center;
  }
}
.hero {
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -20px;
  margin-bottom: 31px;
  position: relative;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .hero {
    margin-bottom: 40px;
    background-position: 50% -67px;
    background-image: url("../img/hero-desktop.jpg");
    background-size: 768px auto;
  }
  .hero::after {
    content: "";
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: cover;
    display: block;
    width: 100%;
    height: 24px;
    position: absolute;
    left: 0;
    bottom: 0;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-desktop@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .hero {
    margin-bottom: 54px;
  }
  .hero::after {
    height: 34px;
    bottom: -2px;
  }
}
.hero__list {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .hero__list {
    flex-direction: row;
    padding: 0 50px;
    max-width: 1150px;
    width: 100%;
    margin: 0 auto;
  }
}
@media (min-width: 1150px) {
