}
@media (min-width: 768px) {
  .hero__title-wrapper {
    padding-top: 155px;
    padding-bottom: 372px;
  }
}
@media (min-width: 1150px) {
  .hero__title-wrapper {
    padding-top: 157px;
    padding-bottom: 355px;
  }
}
.hero__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin-top: 0;
  font-size: 27px;
  text-align: center;
