}
.hero__link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 103px;
  text-decoration: none;
  padding: 14px 36px 18px 30px;
}
@media (min-width: 768px) {
  .hero__link {
    min-height: 130px;
    padding: 10px 36px 27px 35px;
  }
  .hero__link--toy {
    padding-right: 37px;
  }
}
.hero__link--interior {
  background-color: #63d1bb;
}
.hero__link--interior:hover {
  background-color: #59d5bc;
}
.hero__link--interior:active::after {
  opacity: 0.7;
}
.hero__link--interior::after {
  background-image: url("../img/interior.svg");
  width: 40px;
  height: 62px;
}
.hero__link--toy {
  background-color: #56c3b2;
  padding-right: 30px;
}
.hero__link--toy:hover {
  background-color: #46c1ae;
}
.hero__link--toy:active::after {
  opacity: 0.7;
}
.hero__link--toy::after {
  background-image: url("../img/toy.svg");
  width: 60px;
  height: 60px;
}
.hero__link::after {
  content: "";
  display: block;
  background-position: center;
  background-repeat: no-repeat;
}
.hero__link:hover {
  transform: translateY(-5px);
}
@media (min-width: 768px) {
  .hero__link:hover {
    min-height: 124px;
  }
}
@media (min-width: 1150px) {
  .hero__link {
    padding-top: 24px;
    padding-bottom: 45px;
  }
  .hero__link--toy {
    padding-right: 34px;
  }
}
.hero__text {
  align-items: center;
  color: #ffffff;
  font-size: 19px;
  line-height: 24px;
